.options {
  display: flex;
  flex-direction: column;
  width: 90vw;
  padding-left: 5vw;
  padding-right: 5vw;
  .options__title {
    font-size: 24px;
    line-height: 24px;
    color: $color-black;
    font-weight: 400;
    font-family: "Oswald";
  }
  .options__container {
    display: flex;
    flex-direction: column;
    width: 90vw;
  }
}

@include tablet {
  .options {
    .options__title {
      font-size: 36px;
      line-height: 36px;
      padding-left: 5vw;
    }
  }
}

@include laptop {
  .options {
    .options__title {
      font-size: 36px;
      line-height: 36px;
    }
    .options__container {
      flex-direction: row;
      justify-content: space-between;
      height: 50vh;
    }
  }
}
