.header {
  width: 100vw;
  display: flex;
  flex-direction: column;
}

@include tablet {
  .header {
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
  }
}

@include laptop {
  .header {
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
  }
}
